body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ui.sidebar.overlay.sidebar-dark {
	background: rgba(0, 0, 0, 0.7);
	color: #ccc;
	width: 600px;
	padding: 20px;
}

.ui.header {
	color: #fff;
}

.info-general {
	margin-bottom: 24px;
}
.info-list-headline {
	margin-top: 8px;
}
.general-index-title {
	font-size: 16px;
	padding-bottom: 4px;
}


p.info-region-headline {
	margin-bottom: 8px;
}

.accordion-item .info-title {
	font-size: 16px;
	font-weight: bold;
}

.accordion-item .info-time {
	font-size: 12px;
	margin-left: 22px;
}
.accordion-item-title {
	margin-left: 16px;
}

.info-pref h3.header {
	margin-top: 8px;
}
.info-pref > p {
	margin-top: 8px;
}

.info-breadcrumb.breadcrumb {
	margin-top: 6px;
	float: right;
}
.info-breadcrumb.breadcrumb .divider {
	color: #999;
}
.info-breadcrumb.breadcrumb a {
	color: #999;
}
.info-breadcrumb.breadcrumb a:hover {
	color: #444;
}

.info-detail {
	white-space: pre-wrap;
	margin-bottom: 10px;
}


.info-header {
	margin-bottom: 10px;
}
.info-header button.info-header-link.ui.inverted.button {
	font-size: 0.7em;
	margin-top: 6px;
	box-shadow: none!important;
}
.info-header button.info-header-link.ui.inverted.button:hover,
.info-header button.info-header-link.ui.inverted.button:focus {
	background: inherit;
	color: inherit;
}

/* popup-dark */
.mapboxgl-popup-content { background: rgba(0, 0, 0, 0.7); color:#fff; padding: 4px 10px; text-align: center; line-height:18px; }
.mapboxgl-popup-content span { color:#ccc; font-size: 11px; }
.mapboxgl-popup-anchor-top .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-top-right .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-top-left .mapboxgl-popup-tip { border-bottom-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-bottom .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-bottom-right .mapboxgl-popup-tip,
.mapboxgl-popup-anchor-bottom-left .mapboxgl-popup-tip { border-top-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-right .mapboxgl-popup-tip { border-left-color:rgba(0, 0, 0, 0.7); }
.mapboxgl-popup-anchor-left .mapboxgl-popup-tip { border-right-color:rgba(0, 0, 0, 0.7); }

